body {
  background: #012050 !important;
  margin: 0 auto;
}

.container {
  min-height: 200px;
  text-align: center;
  margin: 20px;
}

.ant-card {
  width: 500px;
  margin: 0px auto;
}

@media (max-width: 550px) {
  .ant-card {
    width: 98%;
  }
}

.input-text {
  outline: none;
  border: none;
  border-radius: 0px;
  -webkit-transition: all 100ms;
  transition: all 100ms;
  border-bottom: 1px solid #8080803b;
}

.input-text:hover,
.input-text:focus {
  border-bottom: 1px solid #8080808c;
}

.input-text:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(255, 255, 255) !important;
}

.todo-container .ant-card .text-section {
  line-height: 39px;
  margin-left: 10px;
  word-wrap: break-word;
}

.edit-icon:hover {
  color: orange;
}

.close-icon:hover,
.delete-icon:hover {
  color: red;
}

.ant-card-actions > li {
  margin: 8px 0px;
}

.ant-card-actions > li > span:hover {
  color: gray;
}

.anticon {
  -webkit-transition: all 400ms;
  transition: all 400ms;
}

.todo-container {
  text-align: center;
  margin: 10px auto;
}

[data-react-beautiful-dnd-drag-handle] {
  margin-top: 10px;
}

